"use client";

import React, { useMemo, useState, useCallback } from "react";
import { useRouter } from "next/navigation";
import dynamic from "next/dynamic";
import {
  ZusCard,
  ZusCardHeader,
  ZusCardBody,
  ZusCardFooter,
  ZusHeading,
  ZusText,
  ZusButton,
} from "@/components/zus"; // adjust path if different

// Lazy load chart to keep TTI low
const Bar = dynamic(() => import("react-chartjs-2").then(m => m.Bar), { ssr: false });
import {
  Chart as ChartJS,
  BarElement,
  CategoryScale,
  LinearScale,
  Tooltip,
  Legend,
} from "chart.js";
ChartJS.register(BarElement, CategoryScale, LinearScale, Tooltip, Legend);

function fmtPLN(n: number) {
  return new Intl.NumberFormat("pl-PL", {
    style: "currency",
    currency: "PLN",
    maximumFractionDigits: 0,
  }).format(isFinite(n) ? n : 0);
}

// placeholders; swap with real data later
const ŚREDNIA = 3200;
const WYSOKA = 5200;
const IKZE_UPLIFT = 0.08;
const PPK_UPLIFT = 0.12;

export default function WelcomeStart() {
  const router = useRouter();

  const [ikze, setIkze] = useState(false);
  const [ppk, setPpk] = useState(false);
  const [value, setValue] = useState<number>(4000);

  // enforce steps of 500 for arrows; allow free typing otherwise
  const onNumberKeyDown = useCallback(
    (e: React.KeyboardEvent<HTMLInputElement>) => {
      if (e.key === "ArrowUp" || e.key === "ArrowDown") {
        e.preventDefault();
        setValue(v => {
          const delta = e.key === "ArrowUp" ? 500 : -500;
          const next = Math.max(0, (v || 0) + delta);
          return Math.round(next / 500) * 500;
        });
      }
    },
    []
  );

  const onNumberChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const raw = e.target.value.replace(/\s/g, "").replace(",", ".");
    const num = Number(raw);
    if (Number.isFinite(num)) setValue(num);
    else setValue(0);
  }, []);

  const powered = useMemo(() => {
    let v = value || 0;
    if (ikze) v += v * IKZE_UPLIFT;
    if (ppk) v += v * PPK_UPLIFT;
    return Math.round(v);
  }, [value, ikze, ppk]);

  const chartData = useMemo(
    () => ({
      labels: ["Średnia", "Wysoka", "Twoja"],
      datasets: [
        {
          label: "Kwota (PLN)",
          data: [ŚREDNIA, WYSOKA, value || 0],
          backgroundColor: ["#BFC5CE", "#3F84D2", "#00416E"], // gray / blue / navy from your palette
          borderRadius: 8,
        },
        ...(ikze || ppk
          ? [
              {
                label: "Twoja (z IKZE/PPK)",
                data: [0, 0, powered],
                backgroundColor: "#00993F", // ZUS green
                borderRadius: 8,
              },
            ]
          : []),
      ],
    }),
    [value, powered, ikze, ppk]
  );

  const chartOptions = useMemo(
    () => ({
      responsive: true,
      plugins: {
        legend: { display: true, labels: { usePointStyle: true } },
        tooltip: {
          callbacks: {
            label: (ctx: any) => `${ctx.dataset.label}: ${fmtPLN(ctx.raw as number)}`,
          },
        },
      },
      scales: {
        y: {
          beginAtZero: true,
          ticks: { callback: (v: number) => fmtPLN(v) },
        },
      },
    }),
    []
  );

  const ciekawostka = useMemo(() => {
    if ((value || 0) >= 6000)
      return "Ciekawostka: Najwyższe świadczenia zwykle łączą długi staż i wyższe składki.";
    if ((value || 0) >= 4000)
      return "Ciekawostka: Przesunięcie przejścia o 5 lat często znacząco podnosi świadczenie.";
    return "Ciekawostka: Okresy L4 wpływają na podstawę — w symulatorze możesz je uwzględnić.";
  }, [value]);

  const isValid = (value || 0) > 0;

  return (
    <div className="max-w-6xl mx-auto py-10">
      <ZusCard variant="featured" className="p-0">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-0">
          {/* LEFT COLUMN */}
          <div className="p-6 md:p-8">
            <ZusCardHeader className="p-0">
              <ZusHeading level={1} className="zus-text-h2">
                jaka emerytura?
              </ZusHeading>
              <ZusText className="mt-2 text-neutral-700">
                Podaj kwotę, którą chciał(a)byś otrzymywać. Porównamy ją do średniej i „wysokiej”.
              </ZusText>
            </ZusCardHeader>

            <ZusCardBody className="p-0 mt-6 space-y-5">
              {/* Amount field */}
              <div>
                <label htmlFor="kwota" className="zus-label">
                  Docelowa emerytura (PLN)
                </label>
                <div className="mt-1 flex items-center gap-3">
                  {/* native input so we can set step=500 and custom arrows */}
                  <input
                    id="kwota"
                    type="number"
                    inputMode="numeric"
                    min={0}
                    step={500}
                    value={Number.isFinite(value) ? value : 0}
                    onChange={onNumberChange}
                    onKeyDown={onNumberKeyDown}
                    className="zus-input w-56"
                    aria-describedby="kwota-help"
                  />
                  <span className="zus-badge zus-badge-neutral">{fmtPLN(value || 0)}</span>
                </div>
                <p id="kwota-help" className="zus-text-small mt-1 text-neutral-600">
                  Strzałki ↑/↓ zmieniają kwotę o 500. Wartość może być edytowana ręcznie.
                </p>
              </div>

              {/* Checkboxes */}
              <fieldset>
                <legend className="zus-text-small text-neutral-700">
                  Opcjonalnie włącz wpływ oszczędzania:
                </legend>
                <div className="mt-2 flex flex-col sm:flex-row gap-4">
                  <label className="inline-flex items-center gap-2">
                    <input
                      type="checkbox"
                      className="zus-checkbox"
                      checked={ikze}
                      onChange={(e) => setIkze(e.target.checked)}
                      aria-describedby="ikze-help"
                    />
                    <span>IKZE</span>
                  </label>
                  <label className="inline-flex items-center gap-2">
                    <input
                      type="checkbox"
                      className="zus-checkbox"
                      checked={ppk}
                      onChange={(e) => setPpk(e.target.checked)}
                      aria-describedby="ppk-help"
                    />
                    <span>PPK</span>
                  </label>
                </div>
                <div className="mt-1 grid gap-1">
                  <p id="ikze-help" className="zus-text-small text-neutral-600">
                    IKZE: przykładowy wzrost {Math.round(IKZE_UPLIFT * 100)}% (poglądowo).
                  </p>
                  <p id="ppk-help" className="zus-text-small text-neutral-600">
                    PPK: przykładowy wzrost {Math.round(PPK_UPLIFT * 100)}% (poglądowo).
                  </p>
                </div>
              </fieldset>
            </ZusCardBody>
          </div>

          {/* RIGHT COLUMN */}
          <div className="p-6 md:p-8 border-t md:border-t-0 md:border-l">
            <section aria-labelledby="chart-title" className="h-full flex flex-col">
              <h2 id="chart-title" className="zus-text-h4 mb-3">
                Wykres: średnia, wysoka i Twoja
              </h2>
              <figure aria-describedby="chart-desc" className="flex-1">
                <div
                  role="img"
                  aria-label={`Porównanie: średnia ${fmtPLN(ŚREDNIA)}, wysoka ${fmtPLN(
                    WYSOKA
                  )}, Twoja ${fmtPLN(value || 0)}${
                    ikze || ppk ? `; z IKZE/PPK ${fmtPLN(powered)}` : ""
                  }.`}
                >
                  <Bar data={chartData} options={chartOptions} />
                </div>
                <figcaption id="chart-desc" className="sr-only">
                  Słupki pokazują średnią, wysoką oraz Twoją kwotę. IKZE/PPK dodają dodatkowy słupek.
                </figcaption>
              </figure>
            </section>
          </div>

          {/* BOTTOM ROW (spans both columns) */}
          <div className="md:col-span-2 flex flex-col md:flex-row items-start md:items-center justify-between gap-4 p-6 md:p-8 border-t">
            <div className="zus-alert zus-alert-info max-w-2xl">
              <div className="zus-alert-title">Ciekawostka</div>
              <p className="mt-1">
                {(value || 0) >= 6000
                  ? "Osoby z najwyższymi świadczeniami zwykle pracowały dłużej i miały dłuższe okresy składkowe."
                  : (value || 0) >= 4000
                  ? "Przesunięcie przejścia o 5 lat często istotnie podnosi świadczenie."
                  : "Okresy L4 też wpływają na podstawę — w symulatorze możesz je uwzględnić."}
              </p>
            </div>

            <ZusButton
              variant="primary"
              size="large"
              disabled={!isValid}
              aria-disabled={!isValid}
              onClick={() => router.push("/simulator")}
            >
              kontynuuj
            </ZusButton>
          </div>
        </div>
      </ZusCard>
    </div>
  );
}